import React, {Component} from 'react';
import axios from 'axios';

const restdetail = "https://developerfunnel.herokuapp.com/hotellist";
class CusineFilter extends Component {

    cuisineFilter = (event) => {
        let mealid = (this.props.mealIdNumber)
        let cuisine = (event.target.value)
        
        let url;
        if(cuisine == ""){
            url = `${restdetail}/${mealid}`
        }else{
            url = `${restdetail}/${mealid}?roomtype=${cuisine}`
        }
        axios.get(url)
        .then((response) => {this.props.restpercuisine(response.data)})
    }

    render(){
        return(
            <React.Fragment>
               <center>Cuisine</center>
                        <hr/>
                        <div onChange={this.cuisineFilter}>
                        <label className="radio">
                                <input type="radio" value="" name="cuisine"/>All
                            </label>
                            <label className="radio">
                                <input type="radio" value="1" name="cuisine"/>Deluxe Room
                            </label>
                            <label className="radio">
                                <input type="radio" value="2" name="cuisine"/>Premiere Rooms
                            </label>
                            <label className="radio">
                                <input type="radio" value="3" name="cuisine"/>Budget Room   
                            </label>
                            <label className="radio">
                                <input type="radio" value="4" name="cuisine"/>Semi Deluxe Room 
                            </label>
                            <br/>
                        </div>
            </React.Fragment>
        )               
    }
}


export default CusineFilter