import React, {Component} from 'react';
import axios from 'axios';

const restdetail = "https://developerfunnel.herokuapp.com/hotellist";
class CostFilter extends Component {
    
    costFilter = (event) => {
        let mealid =(this.props.mealIdNumber)
        let cost = (event.target.value).split(',');
        let lcost = Number(cost[0]);
        let hcost = Number(cost[1]);
        let url;
        if(event.target.value==""){
            url = `${restdetail}/${mealid}`
            console.log(url)
        }else{
            url = `${restdetail}/${mealid}?hcost=${hcost}&lcost=${lcost}`
            console.log(url)
        }
        axios.get(url)
        .then((response) => {this.props.restpercost(response.data)})
    }

    render(){
        return(
            <React.Fragment>
                <center>Cost To Filter</center>
                <hr/>
                <div onChange={this.costFilter}>
                        <label className="radio">
                                <input type="radio" value="" name="range"/>All
                            </label>
                            <label className="radio">
                                <input type="radio" value="1000,3000" name="range"/>1000-3000
                            </label> 
                            <label className="radio">
                                <input type="radio" value="3001,6000" name="range"/>3001-6000
                            </label> 
                            <label className="radio">
                                <input type="radio" value="6001,10000" name="range"/>6001-10000
                            </label> 
                </div>
            </React.Fragment>
        )               
    }
}


export default CostFilter