import React,{Component} from 'react';
import axios from 'axios';
import DetailDisplay from './DetailsDisplay';
import CostFilter from '../filter/costFilter';
import CusineFilter from '../filter/cuisineFilter';

const restdetail = "https://developerfunnel.herokuapp.com/hotellist";
class Details extends Component {
    constructor(props){
        super(props)

        this.state={
            citiId: (sessionStorage.getItem('citiId'))? (sessionStorage.getItem('citiId')):1,
            restlist:[],
        }
    }

    setDataPerCuisine(sortedData){
        this.setState({restlist:sortedData})
    }

    setDataPerCost(sortedData){
        this.setState({restlist:sortedData})
    }
    render(){
        return(
            <div>
                <div className="row">
                    <div className="col-md-2">
                        <CusineFilter mealIdNumber={this.props.match.params.id} restpercuisine={(data) => {this.setDataPerCuisine(data)}}/>
                        <CostFilter restpercost={(data) => {this.setDataPerCost(data)}} mealIdNumber={this.props.match.params.id}/>
                    </div>
                    <div className="col-md-10">
                        <DetailDisplay restdata={this.state.restlist}/> 
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount(){
        sessionStorage.setItem('type',this.props.match.params.id)
        let typeid = parseInt(this.props.match.params.id)
        axios.get(`${restdetail}/${typeid}`)
        .then((response) => {this.setState({restlist:response.data})})
    }
}


export default Details;