import React from 'react';
import {Link} from 'react-router-dom';
import './detailslist.css';

const DetailDiaplay = (props) => {
    console.log(props)

    const renderTable = ({restdata}) => {
        if(restdata){
            return restdata.map((item) => {
                return(
                    <div class="Item" id={item.id}>
                            <div class="row row-height">
                                <div class="col-sm-3">
                                    <img class="Image" src={item.thumb} />
                                </div>
                                <div class="col-sm-9">
                                    <div class="The-Big-Chill-Cakery">
                                    <Link to={`/rest/${item._id}`}>{item.name}</Link></div>
                                    <div class="FORT">{item.city_name}</div>
                                    <div class="Shop-1-Plot-D-Samruddhi-Complex-Chincholi- ">{item.locality}</div>
                                </div>
                            </div>
                            <hr />
                            <div class="row">
                                <div class="col-sm-3">
                                    <div class="CUISINES-COST-FOR-TWO">Room Type</div>
                                    <div class="CUISINES-COST-FOR-TWO">COST FOR Night</div>
                                </div>
                                <div class="col-sm-9">
                                    <div class="Bakery-700">
                                        {item.type[0].name},{item.type[1].name},{item.type[2].name}</div>
                                    <div class="Bakery-700">{item.cost}</div>
                                </div>
                            </div>
                        </div>
                )
            })
        }
    }
    return(
        <React.Fragment>
           
            <div class="container-fluid">
            <div class="header-search">Select Hotels with filter</div>
            <div class="row">
                <div class="col-sm-9 col-md-6 col-lg-9">
                {renderTable(props)}  
                </div>
            </div>
            </div >
        </React.Fragment>
    )
}

export default DetailDiaplay;