import React, {Component} from 'react';
import QuickSearchDisplay from './QuickSearchDisplay';

const quickUrl = "https://developerfunnel.herokuapp.com/booking"

class QuickSearch extends Component {
    constructor(){
        super()

        this.state={
            tripType:''
        }
    }
    render(){
        return(
           <div>
               <QuickSearchDisplay tripData={this.state.tripType}/>
           </div>
        )
    }

    componentDidMount(){
        fetch(quickUrl,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({tripType:data})
        })
    }
}

export default QuickSearch;