import React,{Component} from 'react';
import './search.css';

const locurl = "https://developerfunnel.herokuapp.com/location";
const resturl = "https://developerfunnel.herokuapp.com/hotels?city=";

class Search extends Component {
    constructor(props){
        super(props)
        this.state={
            location:'',
            city:'',
            restaurents:''
        }
    }

    handleCity = (event) => {
        this.setState({restaurents:''})
        this.setState({city:event.target.value})
        var cityid = (event.target.value)
        sessionStorage.setItem('citiId',event.target.value)
        fetch(`${resturl}${cityid}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({restaurents:data})
        })
    }

    renderCity = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city}>
                        {item.city_name}
                    </option>
                )
            })
        }
    }

    renderRest = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id}>
                         {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }

    handleRestaurent =(event) => {
        console.log(event.target.value)
        this.props.restid(Number(event.target.value))
    }

    render(){
        return(
            <header>
                <div class="imageContainer">
                    <div id="logo">
                        <b>D!</b>
                    </div>
                    <div class="heading">
                        Plan Your Trip With Us.
                    </div>
                    <div class="locationSelector">
                        <select onChange={this.handleCity} style={{height:50}}>
                        <option>--------Select City------</option>
                        {this.renderCity(this.state.location)}
                        </select>
                        <select id="restaurent" onChange={this.handleRestaurent}
                        style={{height:50}}>
                        <option>--------Select Hotel------</option>
                        {this.renderRest(this.state.restaurents)}
                        </select>
                    </div>
                </div>
                <hr/>
            </header>
        )
    }

    componentDidMount(){
       fetch(locurl,{method:'GET'})
       .then((res) => res.json())
       .then((data) => {
          
           this.setState({location:data})
       })
    }
    
}

export default Search;

